import React from "react";
import { Link } from "react-router-dom";

const Navbar = () => {
    return (
        <nav>
            <Link to="/">Home</Link>
            <Link to="/issue">Issue Certificate</Link>
            <Link to="/verify">Verify Certificate</Link>
            <Link to="/revoke">Revoke Certificate</Link>
        </nav>
    );
};

export default Navbar;
