import Web3 from "web3";
import contractABI from "../contract/contractABI.json";

// Check if MetaMask is available
if (!window.ethereum) {
  alert("MetaMask is not installed! Please install it.");
  throw new Error("MetaMask not found");
}

const web3 = new Web3(window.ethereum);

// Request user accounts and log them
async function getAccount() {
  try {
    const accounts = await window.ethereum.request({ method: "eth_requestAccounts" });
    if (accounts.length === 0) {
      alert("No accounts found. Please connect MetaMask.");
      throw new Error("No MetaMask accounts found");
    }
    console.log("Connected Account:", accounts[0]);
    return accounts[0];
  } catch (error) {
    console.error("Error fetching account:", error);
    alert("Failed to connect MetaMask. Please check your connection.");
  }
}

const contractAddress = process.env.REACT_APP_CONTRACT_ADDRESS;
if (!contractAddress) {
  throw new Error("Contract address is missing. Check your .env file.");
}

const contract = new web3.eth.Contract(contractABI, contractAddress);

export { web3, contract, getAccount };
